
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "myRegister",
components: {
ViewBox,
XHeader,
Qrcode,
XInput,
Group,
XButton
},
created() {
if (this.$route.query.inviteCode) {
window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxa5b0807540a1517e&redirect_uri=http%3A%2F%2Fhaoyi.tlong.tv%2Fregister&response_type=code&scope=snsapi_userinfo&state=' + this.$route.query.inviteCode + '#wechat_redirect';
}
},
mounted () {

this.wxCode = this.$route.query.code;
this.invideCode = this.$route.query.state;
},
data() {
return {
userInfo: "",
phone: "",
password: "",
captcha: "",
show: true,
count: '',
timer: null,
invideCode: '',
wxCode: ''
};
},
methods: {
queryUser() {
this.$vux.loading.show();
this.$api.queryUser({}).then(data => {
if (data.code === 200) {
this.userInfo = data.data.user;
}
this.$vux.loading.hide();
});
},
getCode() {
const TIME_COUNT = 60;
if (this.phone == "") {
this.$vux.toast.text("手机号不得为空", "top");
return;
}
if (!this.timer) {
this.count = TIME_COUNT;
this.show = false;
this.sendSms();
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--;
} else {
this.show = true;
clearInterval(this.timer);
this.timer = null;
}
}, 1000);
}
},
sendSms() {
this.$api.sendSms({
mobile: this.phone
}).then(data => {
if (data.code === 200) {
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
goRegister() {
this.$api.register({
mobile: this.phone,
captcha: this.captcha,
password: this.password,
code: this.invideCode,
wechatCode: this.wxCode
}).then(data => {
if (data.code === 200) {
this.$vux.toast.text('注册成功', "top");
localStorage.setItem('token', data.data.jwt);
this.$router.push('/attention');
} else {
this.$vux.toast.text(data.message, "top");
}
});
}
}
};



